body{
    display:flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100%;
    background: #2d2d2d;
}

i{
    cursor: pointer;
}

/* Remove All Button Styling  */

.remove-all{
    position: relative;
}

.remove-all::before{
    display: none;
    content: "REMOVE All";
    position: absolute;
    transform: translateY(100%);
    color: #fff;
    font-size: 1rem;
    font-weight: 600;
    overflow: hidden;
    
}

.checklist:hover .remove-all::before{
    transition: all 0.3s linear;
    display: block;
    transform: translateY(0%);
}

.checklist:hover .remove-all{
    transition: all 0.3s linear;
    background: #000;
}



.eachItem:hover .fa-edit{
    color: rgb(100, 214, 47)
}

.eachItem:hover .fa-trash-alt{
    color: rgb(214, 47, 61);
}